/*    */
.customer-center {
  width: 100vh;
  margin: 0 auto;
  /*   */
  padding: 40px 20px;
  /*   */
  color: #333;
  /*   */
  background-color: white;
  /*   */
}

/*    */
.customer-center h1 {
  font-size: 2rem;
  margin-bottom: 20px;
}

/* FAQ      */
.customer-center .faq ul {
  list-style: none;
  padding-left: 0;
}

/* FAQ     */
.customer-center .faq li {
  margin-bottom: 10px;
}

/*   form   */
.customer-center .contact-form form {
  display: flex;
  flex-direction: column;
  /*   */
  gap: 10px;
  /*    */
}

/* (input, textarea)   */
.customer-center input,
.customer-center textarea {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
}

/*    */
.customer-center button {
  background-color: var(--text-color);
  /*    */
  color: white;
  border: none;
  padding: 12px;
  border-radius: 6px;
  cursor: pointer;
}

/*    */
.customer-center .contact-info {
  margin-top: 30px;
  font-size: 0.95rem;
  line-height: 1.6;
  color: #444;
}


/*       */
.customer-close-button {
  position: absolute;
  /*     */
  top: 10px;
  right: 15px;
  background: none;
  border: none;
  font-size: 1.5rem;
  font-weight: bold;
  cursor: pointer;
  color: #888;
}

.customer-close-button:hover {
  color: #f7374f;
  /* hover   */
}

/*       */
.customer-center {
  position: relative;
}